@media (max-width: 1400px) {
   .menu {
      &_item {
         padding-right: 30px; } } }

@media (max-width: 1199px) {
   .menu {
      &_link {
         font-size: 13px; }
      &_item {
         padding-right: 20px; } }
   .promo {
      h1 {
         font-size: 50px; } }
   .reasons_block {
      align-items: flex-start; }
   .mobile {
      &_item {
         width: 290px;
         &_subtitle {
            font-size: 22px; } } }
   .reasons_descr {
      width: 310px; }
   .footer {
      &_wrapper > div {
         width: 50%;
         &:nth-child(1) {
            width: 25%; }
         &:nth-child(3) {
            width: 25%;
            display: flex;
            justify-content: flex-end; } }
      &_mobile {
         display: flex;
         a {
            margin: 0 3px;
            img {
               width: 100%; } } } } }

@media (max-width: 991px) {
   .menu {
      &_link {
         font-size: 10px; }
      &_item {
         padding-right: 10px; } }
   .subheader {
      &_logo {
         img {
            width: 100%; } }
      &_official {
         font-size: 11px; }
      &_phone {
         font-size: 18px; }
      &_btn {
         margin-top: 23px;
         height: 40px;
         width: 100%; } }
   .promo {
      h1 {
         font-size: 40px; }
      &_subheader {
         font-size: 26px; } }
   .reasons_block {
      flex-direction: column;
      align-items: center; }
   .reasons_round {
      margin-right: 0; }
   .reasons_descr {
      margin-top: 20px;
      width: 310px; }
   .mobile_item {
      width: 216px;
      height: 240px;
      &_subtitle {
         font-size: 18px; } }
   .choice {
      &_img {
         width: 100%; } }
   .footer {
      &_wrapper {
         flex-wrap: wrap; }
      &_wrapper > div {
         width: 100%;
         display: flex;
         justify-content: center;
         margin-bottom: 20px;
         &:nth-child(1) {
            width: 100%; }
         &:nth-child(3) {
            width: 100%;
            justify-content: center; } } } }

@media (max-width: 767px) {
   .menu {
      position: fixed;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      top: 0;
      left: -100%;
      width: 50%;
      height: 100vh;
      padding: 20px 15px;
      background-color: rgba(0, 0, 0, .8);
      transition: 0.6s all;
      &_active {
         left: 0; }
      &_item {
         padding-right: 0;
         border: none;
         margin: 10px 0; }
      &_link {
         font-size: 20px; } }
   .hamburger {
      display: block;
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translateY(-50%);
      width: 26px;
      height: 12px;
      span {
         display: block;
         height: 2px;
         width: 100%;
         background-color: #fff;
         margin-bottom: 2px;
         transition: 0.5s all; }
      &_active {
         span {
            margin-bottom: -2px;
            &:nth-child(1) {
               transform: translateY(3px) rotate(-45deg); }
            &:nth-child(2) {
               display: none; }
            &:nth-child(3) {
               transform: translateY(3px) rotate(45deg); } } } }
   .xs-hidden {
      display: none; }
   .xs-visible {
      display: block; }
   .subheader {
      &_phone {
         display: block;
         margin-top: 12px;
         color: #FFF;
         font-size: 16px;
         font-weight: 400;
         text-transform: uppercase;
         text-align: center; }
      &_btn {
         display: block;
         width: 200px;
         height: 40px;
         margin: 0 auto;
         margin-top: 12px; } }
   .promo {
      h1 {
         font-size: 40px;
         line-height: 50px; } }
   .mobile {
      padding: 40px 0; } }

@media (max-width: 575px) {
   nav {
      height: 30px; }
   header {
      height: 30px; }
   .subheader {
      height: 62px;
      top: 30px;
      &_official {
         display: none; }
      &_phone {
         font-size: 12px;
         font-weight: 400;
         text-transform: uppercase;
         margin-top: 11px; }
      &_btn {
         padding: 0 9px;
         width: 110px;
         height: 22px;
         font-size: 10px;
         margin-top: 7px; } }
   .promo {
      min-height: 400px;
      padding: 93px 0 35px 0;
      &_descr {
         display: none; }
      h1 {
         font-size: 30px;
         padding: 0 24px;
         line-height: 35px; }
      &_subheader {
         font-size: 20px;
         margin-top: 33px;
         padding: 0 10px;
         line-height: 30px; }
      &_btn {
         width: 210px;
         height: 50px;
         margin-top: 72px;
         font-size: 16px; } }
   .menu {
      width: 85%; }
   .advantages {
      padding: 22px 0 38px 0;
      .label {
         width: 110px;
         height: 22px;
         padding: 5px 10px; }
      .title {
         font-size: 16px;
         margin-top: 15px;
         line-height: 16px; }
      .reasons_block {
         margin-top: 26px; }
      .reasons_descr {
         width: 100%;
         margin-top: 27px; }
      .reasons_round {
         width: 100px;
         height: 100px; }
      .reasons_subtitle {
         font-size: 16px; } }
   .hamburger {
      width: 20px; }
   .mobile {
      padding: 28px 0 13px 0;
      .title {
         margin-top: 16px;
         font-size: 16px; }
      .subtitle {
         font-size: 14px;
         margin-top: 9px;
         line-height: 18px; }
      &_item {
         width: 290px;
         height: 254px;
         margin-bottom: 30px;
         padding: 80px 44px 0 44px;
         &_plus {
            width: 29px;
            height: 29px;
            bottom: 21px; } } }
   .choice {
      padding: 26px 0 31px 0;
      background: #070716;
      min-height: 393px;
      .black {
         display: none; }
      .title {
         display: none; }
      .subtitle {
         display: none; }
      &_img {
         margin-top: 36px; }
      &_descr {
         color: #FFF;
         margin-top: 23px; } }
   .require {
      padding: 24px 0 30px 0;
      .title {
         margin-top: 16px;
         font-size: 16px; }
      &_block {
         margin: 30px 0; }
      &_title {
         font-size: 16px; }
      &_list {
         margin-top: 3px;
         padding-left: 31px;
         li {
            font-size: 14px;
            margin-bottom: 5px; } } }
   .map {
      padding: 24px 0 30px 0;
      min-height: 360px;
      .title {
         font-size: 16px;
         margin-top: 16px;
         line-height: 16px; }
      .subtitle {
         font-size: 14px;
         margin-top: 11px; }
      &_link {
         margin-top: 212px; } }
   footer {
      min-height: 255px;
      padding: 25px 0 20px 0;
      .divider {
         width: 255px;
         height: 6px; } }
   .footer {
      &_wrapper {
         padding-top: 22px; }
      &_wrapper > div {
         margin-bottom: 20px;
         &:nth-child(1) {
            order: 2; }
         &:nth-child(2) {
            order: 1; }
         &:nth-child(3) {
            order: 3; } }
      &_links {
         width: 290px;
         &_main {
            padding: 0;
            font-size: 14px;
            line-height: 14px; }
         &_sub {
            flex-wrap: wrap;
            justify-content: center;
            a {
               margin: 0 7px; } }
         &_lang {
            margin-top: 10px; } } } }
