header {
   position: relative;
   height: 52px; }
nav {
   position: fixed;
   width: 100%;
   height: 52px;
   background-color: #212121;
   z-index: 10; }

.hamburger {
   display: none; }

.menu {
   display: flex;
   justify-content: space-between;
   padding: 14px 70px;
   margin: 0;
   list-style-type: none;
   &_link {
      color: #fff;
      font-size: 15px;
      font-weight: 300;
      text-decoration: none; }
   &_item {
      border-right: 2px dotted black;
      padding-right: 45px;
      &:last-child {
         border-right: none; } } }
.subheader {
   position: absolute;
   top: 52px;
   width: 100%;
   height: 87px;
   background-color: rgba(0, 0, 0, 0.6);
   &_logo {
      display: block;
      max-width: 100%;
      margin-top: 19px; }
   &_official {
      color: #fff;
      font-size: 13px;
      font-weight: 100;
      margin-top: 6px; }
   &_col {
      margin-top: 19px;
      color: #fff;
      text-align: right;
      font-size: 12px;
      font-weight: 300; }
   &_phone {
      margin-top: 6px;
      display: block;
      text-align: right;
      color: #fff;
      font-size: 22px;
      font-weight: 400;
      text-decoration: none;
      line-height: 12px; }
   &_btn {
      margin-top: 20px;
      border-radius: 4px;
      background: linear-gradient(0deg, #1eacc7 0%, #4ce2ff 100%);
      box-shadow: inset -3px 0px 0px rgba(0, 0, 0, 0.12);
      border: none;
      width: 172px;
      height: 46px;
      color: #fff;
      text-align: center;
      text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
      font-family: Roboto;
      font-size: 14px;
      font-weight: 400;
      text-transform: uppercase;
      @include btn_hover; } }

.promo {
   min-height: 800px;
   background: url("../img/uber_bg.jpg") center center/cover no-repeat;
   padding: 150px 0 185px 0;
   h1 {
      color: #fff;
      font-size: 60px;
      font-weight: 700;
      text-transform: uppercase;
      line-height: 60px;
      margin: 0;
      text-align: center; }
   &_subheader {
      color: #fff;
      font-size: 35px;
      font-weight: 700;
      text-transform: uppercase;
      line-height: 36px;
      margin: 0;
      margin-top: 22px;
      text-align: center; }
   &_descr {
      color: #fff;
      text-align: center;
      font-size: 18px;
      font-weight: 400;
      line-height: 24px;
      margin-top: 16px; }
   &_btn {
      display: block;
      margin: 115px auto 0 auto;
      width: 259px;
      height: 63px;
      border-radius: 4px;
      background: linear-gradient(0deg, #1eacc7 0%, #4ce2ff 100%);
      box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
      border: none;
      color: #fff;
      text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
      font-size: 18px;
      font-weight: 400;
      text-transform: uppercase;
      @include btn_hover; } }
.advantages {
   min-height: 850px;
   padding: 20px 0 67px 0; }
.title {
   position: relative;
   margin-top: 32px;
   margin-bottom: 0;
   color: #222;
   text-align: center;
   font-size: 38px;
   font-weight: 700;
   text-transform: uppercase;
   z-index: 2;
   &_white {
      color: #fff;
      line-height: 38px; } }
.subtitle {
   position: relative;
   color: #222;
   text-align: center;
   font-family: Roboto;
   font-size: 17px;
   font-weight: 400;
   margin-top: 19px;
   z-index: 2;
   &_white {
      color: #fff;
      line-height: 17px; } }
.label {
   position: relative;
   width: 113px;
   height: 24px;
   background-color: black;
   padding: 5px 12px;
   margin: 0 auto;
   color: #fff;
   text-align: center;
   font-size: 11px;
   font-weight: 300;
   text-transform: uppercase;
   z-index: 2;
   &_white {
      background-color: #fff;
      color: #000; } }
.reasons_block {
   display: flex;
   justify-content: center;
   align-items: center;
   margin-top: 35px; }
.reasons_round {
   display: flex;
   justify-content: center;
   align-items: center;
   margin-right: 25px;
   width: 113px;
   height: 113px;
   background-color: #1eacc7;
   border-radius: 100%; }
.reasons_descr {
   width: 343px; }
.reasons_subtitle {
   color: #1eacc7;
   font-size: 17px;
   font-weight: 400;
   line-height: 21px; }
.reasons_text {
   color: #202020;
   font-size: 15px;
   font-weight: 300;
   line-height: 21px; }
.mobile {
   min-height: 864px;
   padding: 48px 0 79px 0;
   background: url('../img/mobile_bg.png') center center/cover no-repeat;
   .row {
      margin-top: 32px; }
   &_item {
      position: relative;
      width: 320px;
      height: 280px;
      margin: 0 auto;
      margin-bottom: 19px;
      padding-top: 92px;
      &_1 {
         background: url('../img/mobile/mobile_1.jpg') center center/cover no-repeat; }
      &_2 {
         background: url('../img/mobile/mobile_2.jpg') center center/cover no-repeat; }
      &_3 {
         background: url('../img/mobile/mobile_3.jpg') center center/cover no-repeat; }
      &_4 {
         background: url('../img/mobile/mobile_4.jpg') center center/cover no-repeat; }
      &_5 {
         background: url('../img/mobile/mobile_5.jpg') center center/cover no-repeat; }
      &_6 {
         background: url('../img/mobile/mobile_6.jpg') center center/cover no-repeat; }
      &_subtitle {
         color: #ffffff;
         font-size: 24px;
         font-weight: 400;
         text-transform: uppercase;
         text-align: center;
         line-height: 31px; }
      &_plus {
         display: flex;
         justify-content: center;
         align-items: center;
         position: absolute;
         bottom: 35px;
         left: 50%;
         transform: translateX(-50%);
         width: 26px;
         height: 26px;
         background-color: #000000;
         margin: 0 auto;
         border-radius: 100%;
         cursor: pointer;
         &:before {
            content: "";
            display: block;
            width: 14px;
            height: 2px;
            background-color: #1eacc7;
            transition: 0.3s all; }
         &:after {
            content: "";
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            display: block;
            width: 2px;
            height: 14px;
            background-color: #1eacc7;
            transition: 0.3s all; }
         &:hover {
            &:before, &:after {
               background-color: #ff5656; } } } } }
.choice {
   position: relative;
   min-height: 895px;
   padding: 57px 0 138px 0;
   background: url('../img/mobile_bg.png') center center/cover no-repeat;
   &_img {
      position: relative;
      display: block;
      margin: 56px auto 0 auto;
      z-index: 2; }
   &_descr {
      text-align: center;
      color: #222222;
      font-size: 17px;
      font-weight: 300;
      line-height: 24px; }
   .black {
      position: absolute;
      top: 0;
      left: 0;
      height: 499px;
      width: 100%;
      background-color: #070716;
      z-index: 1; } }

.require {
   padding: 68px 0 84px 0;
   &_block {
      margin: 33px 0;
      &_nbm {
         margin-bottom: 0px; } }
   &_title {
      color: #1CA8C3;
      font-size: 22px;
      font-weight: 400; }
   &_list {
      list-style-type: none;
      padding-left: 50px;
      margin-top: 22px;
      li {
         position: relative;
         font-size: 17px;
         font-weight: 300;
         line-height: 24px;
         margin-bottom: 10px;
         &:before {
            content: "";
            display: block;
            position: absolute;
            left: -32px;
            top: 3px;
            width: 21px;
            height: 20px;
            background: url('../icons/check_blue.png') center center/cover no-repeat; } } }
   &_descr {
      margin-top: 25px;
      font-size: 17px;
      font-weight: 300;
      line-height: 24px;
      color: #1ca8c3; }
   .warning {
      .require_title {
         color: #ff5656; }
      .require_list {
         li {
            &:before {
               background: url('../icons/check_red.png') center center/cover no-repeat; } } } } }

.map {
   min-height: 717px;
   padding: 49px 0;
   background: url('../img/map.jpg') center center/cover no-repeat;
   .label_small {
      width: 76px;
      height: 24px;
      padding: 5px 18px; }
   &_link {
      display: block;
      margin-top: 486px;
      text-align: center;
      color: #1fbad6;
      font-size: 15px;
      font-weight: 400;
      text-transform: uppercase;
      text-decoration: none; } }

footer {
   min-height: 156px;
   padding: 14px 23px 39px 23px;
   a {
      text-decoration: none; }
   .divider {
      width: 299px;
      height: 4px;
      background-color: #1fbad6;
      margin: 0 auto; } }

.footer {
   &_wrapper {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      padding-top: 32px; }
   &_wrapper > div {
      width: 33%;
      &:nth-child(3) {
         display: flex;
         justify-content: flex-end; } }
   &_social {
      width: 160px;
      display: flex;
      justify-content: space-between;
      &_item {
         display: flex;
         justify-content: center;
         align-items: center;
         margin: 0 4px;
         width: 32px;
         height: 32px;
         background-color: #070716;
         border-radius: 100%;
         i {
            color: #fff; } } }
   &_links {
      width: 390px;
      margin: 0 auto;
      &_main {
         display: flex;
         justify-content: space-between;
         padding: 0 10px;
         a {
            color: #070716;
            font-size: 15px;
            font-weight: 300;
            text-transform: uppercase; } }
      &_sub {
         display: flex;
         justify-content: space-between;
         margin-top: 10px;
         a {
            color: #999999;
            font-size: 11px;
            font-weight: 300;
            text-transform: uppercase; } }
      &_lang {
         display: block;
         margin: 0 auto;
         color: #1fbad6;
         font-size: 11px;
         font-weight: 400;
         margin-top: 19px;
         text-transform: uppercase;
         text-align: center; } } }

.xs-visible {
   display: none; }
